import React from 'react'
import {bindActionCreators} from 'redux'
import {connect} from 'react-redux'
import {entries} from 'reducer'
import {xiaomeiActions} from 'action'
import {BackHome} from 'components'
import './index.scss'

interface P {
    className?: string,
    history: any,
    match: any,
    getNoticeInfoDetail: Function,
    noticeInfoDetail: any
}

interface S {

}

class NoticeInfoDetail extends React.Component<P, S> {
    // 构造
    constructor(props: any) {
        super(props);
        // 初始状态
        this.state = {};
    }

    render(): React.ReactElement<any, string | React.JSXElementConstructor<any>> | string | number | {} | React.ReactNodeArray | React.ReactPortal | boolean | null | undefined {
        const {
            props: {
                className = '',
                noticeInfoDetail
            },
            state: {}
        } = this
        return (
            <div className={'notice_info_detail'}>
                <div className={'content'} dangerouslySetInnerHTML={{__html: noticeInfoDetail.content}} />
                {/*<BackHome />*/}
            </div>
        );
    }

    componentDidMount(): void {
        this.getInit()
    }

    getInit = async () => {
        const {match: {params: {id}}, getNoticeInfoDetail} = this.props
        const {title} = await getNoticeInfoDetail(id)
        document.title = title
    }

    shouldComponentUpdate(nextProps: Readonly<any>, nextState: Readonly<any>, nextContext: any): boolean {
        return true
    }
}

function mapStateToProps(state: entries) {
    const {noticeInfoDetail} = state.xiaomeiReducer
    return {noticeInfoDetail}
}

function mapDispatchToProps(dispatch: any) {
    const {getNoticeInfoDetail} = xiaomeiActions
    return {
        ...bindActionCreators({
            getNoticeInfoDetail
        }, dispatch),
        dispatch
    }
}

export default connect(mapStateToProps, mapDispatchToProps)(NoticeInfoDetail)
